import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "material-ui/styles";
import ButtonBase from "material-ui/ButtonBase";
import Typography from "material-ui/Typography";
import Grid from "material-ui/Grid";

const styles = theme => ({
  root: {
    position: "relative",
    minHeight: 350,
    height: "100vh",
    backgroundImage:
      "url(https://images.pexels.com/photos/461077/pexels-photo-461077.jpeg?w=940&h=650&auto=compress&cs=tinysrgb)",
    backgroundAttachment: "fixed",
    backgroundPosition: "center",
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    "&:hover": {
      zIndex: 1
    },
    "&:hover $backdrop": {
      zIndex: 2,
      opacity: 0.2
    },
    "&:hover $nameMarked": {
      opacity: 0
    },
    "&:hover $myName": {
      opacity: 1,
      zIndex: 3,
      border: "4px solid currentColor"
    },
    "&:hover $myDesc": {
      opacity: 1,
      zIndex: 3
    }
  },
  backdrop: {
    position: "absolute",
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    backgroundColor: theme.palette.common.black,
    opacity: 0.4,
    transition: theme.transitions.create("opacity")
  },
  content: {
    width: "100%",
    height: "100%",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center"
  },
  myName: {
    position: "relative",
    padding: `${theme.spacing.unit * 2}px`,
    color: theme.palette.common.white,
    transition: theme.transitions.create("border")
  },
  nameMarked: {
    position: "relative",
    height: 3,
    width: 200,
    backgroundColor: theme.palette.common.white
  },
  myDesc: {
    position: "relative",
    padding: `${theme.spacing.unit * 2}px`,
    color: theme.palette.common.white
  }
});

class Banner extends React.Component {
    
  render() {
    const { classes, theme } = this.props;

    return (
      <div className={classes.root}>
        <div className={classes.backdrop} />
        <Grid className={classes.content}>
          <ButtonBase focusRipple href="#intro" className="nav">
            <Typography
              variant="display2"
              align="center"
              className={classes.myName}
            >
              Rachanee Saengkrajai
            </Typography>
          </ButtonBase>
          <span className={classes.nameMarked} />

          <Typography
            variant="headline"
            align="center"
            className={classes.myDesc}
          >
            A Lifelong learning developer<br />
            who is skilled, enthusiastic and innovative.
          </Typography>
        </Grid>
      </div>
    );
  }
}

Banner.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired
};

export default withStyles(styles, { withTheme: true })(Banner);
