import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "material-ui/styles";

import Header from "./Header";
import Body from "./Body";

const styles = {
  root: {
    flexGrow: 1,
    widht: "100%",
    minHeigth: 700,
    zIndex: 1,
    overflow: "hidden",
    position: "relative",
    display: "flex"
  }
};

class Layout extends React.Component {

  constructor(props){
    super(props);
    this.onClickNav = this.onClickNav.bind(this);
  }

  componentDidMount() {
    window.addEventListener("resize", this.addNavEventListener);
  }

  addNavEventListener() {
    var elements = document.getElementsByClassName("nav");

    for (var i = 0; i < elements.length; i++) {
      elements[i].addEventListener('click', this.onClickNav, false);
    }
  }

  onClickNav(e) {
    e.preventDefault();

    var targetId = e.target.getAttribute("href");

    if (!targetId) {
      targetId = this.findParentWithAttr(e.target, "href");
    }

    if (targetId) {
      var target = document.getElementById(targetId.substr(1));
      var toPosition = this.getElementOffset(target).top;
      this.scrollTo(
        document.scrollingElement || document.documentElement,
        toPosition,
        400,
        true
      );
    }
  };

  getElementOffset(element) {
    let top = 0,
      left = 0;
    // Loop through the DOM tree
    // and add it's parent's offset to get page offset
    do {
      top += element.offsetTop || 0;
      left += element.offsetLeft || 0;
      element = element.offsetParent;
    } while (element);

    return { top, left };
  }

  scrollTo(parentElm, to, time, prop) {
    if (!parentElm) {
      return;
    }
    var from = window.scrollY;
    var start = new Date().getTime();

    var timer = setInterval(function() {
      var step = Math.min(1, (new Date().getTime() - start) / time);

      if (prop) {
        parentElm["scrollTop"] = from + step * (to - from);
      } else {
        parentElm.style["scrollTop"] = from + step * (to - from);
      }
      if (step === 1) {
        clearInterval(timer);
      }
    }, 25);

    if (prop) {
      parentElm["scrollTop"] = from;
    } else {
      parentElm.style["scrollTop"] = from;
    }
  }

  findParentWithAttr(el, attr) {
    while (el.parentNode) {
      el = el.parentNode;
      if (el.getAttribute(attr)) {
        return el.getAttribute(attr);
      }
    }
    return null;
  }

  render() {
    const { classes } = this.props;

    //<Header />
    return (
      <div className={classes.root}>
        <Body />
      </div>
    );
  }
}

Layout.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(Layout);
